        left: 0;
        right: 0;
        top: 5px;
}
.el-checkbox__input.is-indeterminate .el-checkbox__inner::after {
        display: none;
}
.el-checkbox__inner {
    display: inline-block;
    position: relative;
    border: 1px solid #dcdfe6;
    border-radius: 2px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 14px;
    height: 14px;
