    min-height: 60px;
    text-align: center;
    width: 100%;
    height: 100%;
}
.el-tree__empty-text {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #6d433a;
}
.el-tree__drop-indicator {
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
    background-color: #3C4274;
}
.el-tree-node {
  white-space: nowrap;
