}
.el-alert__title {
    font-size: 13px;
    line-height: 18px;
}
.el-alert__title.is-bold {
      font-weight: bold;
}
.el-alert .el-alert__description {
    font-size: 12px;
    margin: 5px 0 0 0;
}
.el-alert__closebtn {
    font-size: 12px;
